.card_object {
  position: absolute;
}

.no_blue_selection {
  user-select: none;
}

.selected {
  border-width: 1px;
  border-style: dashed;
  border-color: rgb(200, 200, 200);
}

.selected[contenteditable=true] {
  outline: none;
  cursor: text;
}

.selected .dot {
  position: absolute;
  
  height: 10px;
  width: 10px;
  bottom: -8px;
  right: -8px;

  background-color: rgb(200, 200, 200);
  border-radius: 50%;
  border: 2px solid rgb(120, 120, 120)
}